body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  display: flex;
  color: rgb(236, 231, 250);
}

.Part_A {
  width: 20%;
  background-color: #2e4158;
}

.Part_B {
  width: 80%;
  background-color: #66dac7;
  padding: 25px 40px;
  overflow: auto;
}

.Pages {
  color: #707c8b;
  font-weight: 300;
  margin-bottom: 100px;
  margin-top: 50px;
  margin-left: 20px;
}

.Pages__Link {
  color: #707c8b;
  text-decoration: none;
  display: inline-block;
  font-size: 1.7em;
  margin: 0 10px;
  padding-bottom: 5px;
}

.Pages__Link:first-child {
  margin-left: 0;
}

.Pages__Link--Active {
  color: white;
  border-bottom: 1px solid #199087;
}

